* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #2f2d2da6;
}

.container {
    max-width: 1230px;
    padding: 0 15px;
    margin: 0 auto;
}

.card-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 20px;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    max-width: 350px;
    padding-bottom: 20px;
    border: 1px solid #000;
    background: #e2fa83;
}

img {
    width: 100%;
}

.btn-box {
    display: flex;
    column-gap: 10px;
    width: 100%;
    justify-content: center;

    padding: 20px 0;
}

button {
    padding: 7px 10px;
    color: white;
    text-transform: capitalize;
    background: darkblue;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    text-transform: capitalize;
}